import React from 'react';
import { DRUM_KEYS, HEATER_INFO } from '../constant';
import Info from './Info';

export const Drum = () => {
    const [open, setOpen] = React.useState(true);
    const [volume, setVolume] = React.useState(1);
    const [info, setInfo] = React.useState(HEATER_INFO);
    const displayEle = React.useRef(null);
    function playAudio(ele, classList, volume) {
        /**
          @type {HTMLAudioElement}
         **/
        const audio = ele;
        audio.volume = volume;
        displayEle.current.innerText = info[+audio.dataset.idx].id;
        audio
            .play()
            .then(() => {
                setTimeout(() => {
                    classList.remove('active');
                }, 300);
            })
            .catch((err) => console.log(err));
    }
    function handleClick(evt) {
        /**
          @type {HTMLDivElement}
         **/
        const drum = evt.target;
        drum.classList.add('active');
        playAudio(evt.target.children[0], drum.classList, volume);
    }
    function handleKeyDown(evt) {
        if (/^[qweasdzxc]$/i.test(evt.key)) {
            const drum = document.querySelector(
                `#${evt.key.toUpperCase()}`
            ).parentNode;
            const { open, volume } = drum.dataset;
            if (open === 'false') return;
            drum.classList.add('active');

            playAudio(drum.children[0], drum.classList, volume);
        }
    }
    React.useEffect(() => {
        window.addEventListener('keydown', handleKeyDown);
    }, []);

    return (
        <div id="drum-machine">
            <header>
                FCC <i className="fab fa-free-code-camp" aria-hidden="true"></i>
            </header>
            <div className="drum-content">
                <div id="drum-pad">
                    {DRUM_KEYS.map((val, idx) => {
                        return (
                            <div
                                onClick={open ? handleClick : null}
                                key={Math.random()}
                                id={`drum-${val}`}
                                data-open={open}
                                data-volume={volume}
                                className="drum-pad"
                            >
                                {val}
                                <audio
                                    id={val}
                                    className="clip"
                                    data-idx={idx}
                                    src={info[idx].url}
                                ></audio>
                            </div>
                        );
                    })}
                </div>
                <Info
                    {...{
                        setInfo,
                        displayEle,
                        setOpen,
                        setVolume,
                    }}
                />
            </div>
        </div>
    );
};
