import { Slider, Switch } from 'antd';
import React from 'react';
import { HEATER_INFO, PIANO_INFO } from '../constant';
const Info = ({ setOpen, setVolume, displayEle, setInfo }) => {
    return (
        <div className="controls">
            <h2>Power</h2>
            <Switch
                onChange={() => {
                    setOpen((open) => !open);
                    displayEle.current.innerText = '';
                }}
                size="default"
                checkedChildren={<i className="fas fa-check"></i>}
                unCheckedChildren={<i className="fas fa-times"></i>}
                defaultChecked
            />
            <div id="display" ref={displayEle}>
                {displayEle.innerText}
            </div>
            <Slider
                onChange={(value) => {
                    setVolume(value / 100);
                    displayEle.current.innerText = `Volume: ${value}`;
                }}
                defaultValue={100}
                max={100}
                min={0}
                dots={false}
                className="slider"
            ></Slider>
            <h2>Bank</h2>
            <Switch
                onChange={(value) => {
                    if (value) {
                        displayEle.current.innerText = 'Smooth Piano Kit';
                        setInfo(PIANO_INFO);
                    } else {
                        displayEle.current.innerText = "Heater's Kit";
                        setInfo(HEATER_INFO);
                    }
                }}
                size="default"
                checkedChildren={<i className="fas fa-check"></i>}
                unCheckedChildren={<i className="fas fa-times"></i>}
            />
        </div>
    );
};

export default Info;
